<template>
    <div class="agreement-wrap">
        <div v-title data-title="协议">协议</div>
        <header class="logoIn-header">
           <span @click="remove" class="btn-back">取消</span>{{title}}
        </header>
        <div>
            
        </div>
        <div class="agreement-info" v-html="text">
            
        </div>
    </div>
</template>
<script>
    import qs from 'qs'
    export default {
        name : 'agreement',
        data(){
            return {
                title:'服务协议',
                text:''
            
            }
        },
        methods:{
            remove(){
                 this.$router.go(-1);
            },
            agree(){
                this.axios({
                    url:API_URL + 'Home/Index/agreement',
                    method:'get'
                }).then((suc) => {
                   if(suc.data.status == 1){;
                        this.text = suc.data.data;
                   }
                    
                }).catch((err) => {
                    console.log(err);
                });
            }

        },
        mounted() {
            document.body.scrollTop = 0;
            console.log(this.text)
            this.agree();
            
        }
    }
</script>
<style lang="less" scoped>
    .agreement-wrap{
        padding:0 .6rem;
        background:#fff;
        .logoIn-header{
            width:100%;
            height:.9rem;
            line-height:.9rem;
            text-align:center;
            font-size:.36rem;
            color:#d0111b;
            position:relative;
            .btn-back{
                width:1.3rem;
                height:100%;
                font-size:.28rem;
                color:#999;
                position:absolute;
                left:-.6rem;
                top:0;
            }
        }
        .agreement-info{
            margin-top:.3rem;
            color:#666;
            font-size:.2rem;
        }
        
    }
</style>


